h1 {
  font-size: 3rem;
}

.app {
  margin: 0;
  justify-content: center;
  text-align: center;
  /* display: flex;
  flex-direction: column; */
}

.digital-clock {
  /* height: 10vh; */
  background-color: green;
}

.title {
  /* height: 90vh; */
  margin-top: 30vh;
  background-color: red;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
